import React,{useEffect} from 'react'
import { useLazyQuery } from '@apollo/client';
import {useParams } from 'react-router-dom';

import Queries from '../../Utils/Queries/'




import styles from './userDetail.module.css'

const UserDetail = () => {

  const [getUser, result] = useLazyQuery(Queries.USER_DETAIL)
  const userId = localStorage.getItem("userId");

  useEffect(() => {
    getUser({ variables: { userId: parseInt(userId)} })
  }, [getUser, userId ])

  const detail = result.data

  if (!detail) return null

  return (
    <div className={styles.container}>
       <h1>Username:<br/> {detail.userDetailById.username}</h1> <br/>
      <h4>Nombre:<br /> {detail.userDetailById.name} {detail.userDetailById.second_name}</h4>
      <span>Apellido: <br />{detail.userDetailById.last_name} </span>
      <h4>Email:<br /> {detail.userDetailById.email}</h4>
      <h4>Telèfono:<br /> {detail.userDetailById.phone}</h4> 
    </div>
  )
}

export default UserDetail
