import React, { useState} from 'react'

import Paginator from '../Paginator/Paginator'
import Card from '../../Components/Card/Card'

import styles from './products.module.css'





const Products = ({ products }) => {
  
  
  const [currentPage, setCurrentPage] = useState(1)
  const [productPage] = useState(6)
  
  const handlePaged = (e) => {
    if (e.target.id === 'back') {
      if (currentPage >= 1) {
        if (currentPage !== 1)
        setCurrentPage(currentPage - 1);
      }
    } else if (e.target.id === 'next') {
      if (productsToShow.length >= 9)
        setCurrentPage(currentPage + 1);
    } else
      setCurrentPage(Number(e.target.value));
    }
    
  //* ecuación matemtica para hallar los pokemones que voy a mostrar por pagina
  let productsToShow = []
  if (products.length) {
    productsToShow = products.slice(((currentPage - 1) * productPage), (currentPage * productPage))
  }

  
  if(!products) return null

  return (
    <div className={styles.container}>
      <Card products={productsToShow} /> 
      <Paginator
        currentPage={currentPage}
        totalProducts={products.length}
        productPerPage={productPage}
        handlePaged={handlePaged}
      />
    </div>
  )
}

export default Products
